<template>
  <div class="scan">
    <van-notice-bar :scrollable="true" :speed="80">请长按二维码保存至手机后，打开微信“扫一扫”，点击右上角“相册”选择刚保存的二维码进行支付</van-notice-bar>
    <h2 style="line-height: 20px;">扫一扫付款</h2>
    <h5 style="line-height: 0;margin: 10px;">HPay收银台 收款方：Halower</h5>
    <div class="friend-tip">付款后稍等片刻，系统自动反馈付款结果</div>
    <van-row style="line-height: 30px;">
      <van-col span="16">支付票据号: &nbsp; &nbsp;{{id_code}}</van-col>
      <van-col span="8">
        <van-button
          :class="[overtime? 'overtime': '']"
          icon="https://img.yzcdn.cn/vant/logo.png"
          size="small"
          plain
          :disabled="overtime"
          type="primary"
          v-clipboard:copy="id_code"
          v-clipboard:success="onCopy"
        >复制</van-button>
      </van-col>
    </van-row>

    <div class="pay_qr_code">
      <img :class="[overtime? 'overtime': '']" :src="paymentCode" alt />
    </div>
    <div class="pay_qr_code_desc">
      <img :class="[overtime? 'overtime': '']" :src="qrDescSrc" alt />
    </div>
    <van-count-down :time="time" @finish="overtimeNotify">
      <template v-slot="timeData">
        请于
        <span class="item" :class="[overtime? 'overtime': '']">{{ timeData.minutes }}</span> 分
        <span class="item" :class="[overtime? 'overtime': '']">{{ timeData.seconds }}</span> 秒
        内支付
      </template>
    </van-count-down>
    <div class="status-bar">
      <van-button
        :loading="!overtime"
        :disabled="!finished"
        size="large"
        :type="overtime?'danger':'primary'"
        loading-type="spinner"
        :loading-text="loadingTip"
      >{{ loadingTip}}</van-button>
    </div>
  </div>
</template>

<script>
import { Notify } from "vant";
import { setTimeout } from "timers";
import axios from "axios";

export default {
  name: "Scanning",
  mounted() {
    // if (this.$route.params == null || !this.$route.params.id_code) {
    //   this.$router.push({ name: "pending" });
    // }
    this.id_code = this.$route.params.id_code;
    var client = new EventSource(
      `http://xhl.liuchangxi.com/api/pay/stream/${this.$route.params.id}`
    );
    var that = this;
    client.onmessage = msg => {
      if (msg.data == "FINISHED" && that.$route.name == "scan") {
        that.finished = true;
        setTimeout(() => {
          that.$router.push({ name: "success" });
        }, 1500);
      }
    };
  },
  computed: {
    loadingTip: function() {
      if (this.overtime) return "已过期";
      if (!this.overtime && !this.finished) return "等待中...";
      if (!this.overtime && this.finished) return "交易完成";
      else return "未知状态";
    },
    paymentCode: function() {
      switch (this.$route.params.pay_type) {
          case "微信":
            return  `${this.root_path}/wx_pay/wx_${this.$route.params.amount}.jpg`;
          case "支付宝":
            return `${this.root_path}/ali_pay/ali_${this.$route.params.amount}.jpg`;
          default:
            return `${this.root_path}/wx_pay/wx_${this.$route.params.amount}.jpg`;
        }
    },
    qrDescSrc: function() {
      switch (this.$route.params.pay_type) {
          case "微信":
            return   `${this.root_path}/wechat-explain.png`
          case "支付宝":
            return  `${this.root_path}/alipay-explain.png`
          default:
            return  `${this.root_path}/wechat-explain.png`
        }
    }
  },
  data() {
    return {
      root_path: "/pay",
      id_code: "",
      finished: false,
      overtime: false,
      time: 5 * 60 * 1000
    };
  },
  methods: {
    overtimeNotify() {
      (this.overtime = true),
        axios.post("http://xhl.liuchangxi.com/api/pay/status", {
          id: this.$route.params.id,
          trading_status: "OVERTIME"
        });
    },
    onCopy() {
      Notify({
        type: "primary",
        message:
          "复制成功,为了更精确的完成支付流程,请在支付时在备注中输入您的票据号"
      });
    }
  }
};
</script>

<style scoped>
.overtime {
  -webkit-filter: grayscale(100%);
  -moz-filter: grayscale(100%);
  -ms-filter: grayscale(100%);
  -o-filter: grayscale(100%);
  filter: grayscale(100%);
  filter: gray;
}
.scan {
  text-align: center;
}
.scan .pay_qr_code img {
  padding: 10px 10px 10px 10px;
  width: 65%;
}

.scan .pay_qr_code_desc img {
  width: 65%;
  height: 78px;
  margin-bottom: 10px;
}

.item {
  display: inline-block;
  width: 22px;
  margin-right: 5px;
  color: #fff;
  font-size: 12px;
  text-align: center;
  background-color: #1989fa;
}
.status-bar {
  margin-top: 10px;
  margin-bottom: 2px;
}
.friend-tip {
  background-color: #1989fa38;
  color: red;
  color: red;
  font-size: 15px;
  margin-top: 16px;
  margin-bottom: 15px;
}
</style>